import Vue from 'vue'
//Vue是一个构造函数
// 导入vue组件
import App from './App.vue'
// 导入router对象（会先把router文件夹里的index.js里的代码运行）
import router from './router'
// 导入vuex对象
import store from './store'
import '@/styles/common.css'
import './utils/vant-ui.js'
Vue.config.productionTip = false
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
