import Vue from 'vue'
import Vuex from 'vuex'
import user from '@/store/modules/user'
import cart from '@/store/modules/cart'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    //默认地址下标/选中的地址下标
    //主要是方便同级别组件通信啦，在selectAddress组件修改要渲染的地址，点
    //击确认修改index，然后返回订单页面，地址就会同时改变了
    index: 0
  },
  getters: {
    token(state) {
      return state.user.info.token
    }
  },
  mutations: {
    setIndex: (state, index) => {
      state.index = index
    }
  },
  actions: {
  },
  modules: {
    user, cart
  }
})
