import { setUserInfo, getInfo, removeInfo } from '@/api/storage'
export default {
  namespaced: true,
  state: {
    //页面刚加载完成，或者刷新，从本地存储中获取个人信息，更新info
    info: getInfo()
  },
  mutations: {
    setUserInfo(state, info) {
      state.info = info
      //同步保存到本地
      setUserInfo(info)
    }
  }
}